import React, { Component } from 'react';
import { Row, Col, Form, Select, Input,Button } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;



@Form.create({ name: 'formScholarshipApproval' })


class SearchBar1 extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  }

  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={8} >
            <Form.Item label='Ký hiệu phách'>
              {getFieldDecorator('system1', {
              })(
                <Input/>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Số phách từ'>
              {getFieldDecorator('system2', {
              })(
                <Input/>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Cắt số Sinh SV/Phòng'>
              {getFieldDecorator('system3', {
              })(
                <Input/>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Số sinh viên tối đa/túi'>
              {getFieldDecorator('system4', {
              })(
               <Input/>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Túi thi số'>
              {getFieldDecorator('system5', {
              })(
                <Select>
                  <Option value="1">1</Option>
                  <Option value=" 2">2</Option>
                  <Option value=" 3">3</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
        
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
            <Button type="primary">Lập số phách </Button>
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar1



