import React, { Component } from 'react';
import { Row, Col, Form, Select, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch} layout="vertical">
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Hệ">
              {getFieldDecorator('system1', {})(
                <Select>
                  <Option value="1">Đại học chính quy</Option>
                  <Option value="2">Cao đẳng chính quy</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khoa">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">Cơ-điện tử</Option>
                  <Option value="2">Quản trị kinh doanh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học phần">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Học phần 1</Option>
                  <Option value="2">Học phần 2</Option>
                  <Option value="3">Học phần 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lần thi">
              {getFieldDecorator('system4', {})(
                <Select>
                  <Option value=" 1">Thi lần 1</Option>
                  <Option value=" 2">Thi lần 2</Option>
                  <Option value=" 3">Thi lần 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">Thi đợt 1</Option>
                  <Option value=" 2">Thi đợt 2</Option>
                  <Option value=" 3">Thi đợt 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Túi thi số">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                  <Option value="3">3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ký hiệu phách">
              {getFieldDecorator('system7', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số phách từ">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch title="Thêm sinh viên" />
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



